.app-container {
    font-family: Arial, sans-serif;
    margin: 0 auto;
    padding: 0 20px;
    max-width: 1200px;
  }
  
  header, footer {
    background-color: #F5F5F5;
    padding: 10px;
    margin-bottom: 20px;
  }
  
  h1 {
    margin: 0;
  }
  
  h2, h3 {
    color: #333;
  }
  
  table {
    border-collapse: collapse;
    width: 100%;
    margin-top: 10px;
  }
  
  table, th, td {
    border: 1px solid #ccc;
    text-align: left;
    padding: 8px;
  }
  